'use client';

import Link from "next/link";
import Image from "next/image";
import { usePathname } from "next/navigation";
import { useAuth } from "@/contexts/AuthContext";
import { BellIcon, HomeIcon, ShipWheelIcon, UsersIcon } from "lucide-react";

const Sidebar = () => {
    const { user } = useAuth();
    const pathname = usePathname();

    return (
        <aside className="w-64 bg-base-200 border-r border-base-300 hidden lg:flex flex-col h-screen sticky top-0">

            {/* LOGO SECTION */}
            <div className="p-5 border-b border-base-300">
                <Link href="/" className="flex items-center gap-2.5">
                    <ShipWheelIcon className="size-9 text-primary" />
                    <span className="text-3xl font-bold font-mono bg-clip-text text-transparent bg-gradient-to-r from-primary to-secondary tracking-wider">
                        LINGUEX
                    </span>
                </Link>
            </div>

            {/* NAVIGATION SECTION */}
            <nav className="flex-1 p-4 space-y-1">
                <Link
                    href="/"
                    className={`btn btn-ghost justify-start w-full gap-3 px-3 normal-case ${pathname === "/" ? "btn-active" : ""
                        }`}
                >
                    <HomeIcon className="size-5 text-base-content opacity-70" />
                    <span>Home</span>
                </Link>

                <Link
                    href="/notifications"
                    className={`btn btn-ghost justify-start w-full gap-3 px-3 normal-case ${pathname === "/notifications" ? "btn-active" : ""
                        }`}
                >
                    <BellIcon className="size-5 text-base-content opacity-70" />
                    <span>Notifications</span>
                </Link>

                <Link
                    href="/friends"
                    className={`btn btn-ghost justify-start w-full gap-3 px-3 normal-case ${pathname === "/friends" ? "btn-active" : ""
                        }`}
                >
                    <UsersIcon className="size-5 text-base-content opacity-70" />
                    <span>Friends</span>
                </Link>
            </nav>

            {/* USER PROFILE SECTION */}
            <div className="p-4 border-t border-base-300 mt-auto">
                <div className="flex items-center gap-3">
                    <div className="avatar">
                        <div className="w-10 rounded-full relative overflow-hidden">
                            {user?.profilePic && (
                                <Image
                                    src={user.profilePic}
                                    alt="User Avatar"
                                    fill
                                    className="object-cover"
                                    sizes="40px"
                                    priority
                                />
                            )}
                        </div>
                    </div>
                    <div className="flex-1">
                        <p className="font-semibold text-sm">{user?.fullName}</p>
                        <p className="text-xs text-success flex items-center gap-1">
                            <span className="size-2 rounded-full bg-success inline-block" />
                            Online
                        </p>
                    </div>
                </div>
            </div>
        </aside>
    );
};

export default Sidebar;